$imgIconSize: 85px;

.item {
  margin: 10px;

  img {
    width: $imgIconSize;
    height: $imgIconSize;
    border-radius: 25px 25px 25px 25px;
  }

  .appName {
    display: block;
    display: -webkit-box;
    max-width: 100%;
    height: 31px;
    width: 85px;
    margin: 0 auto;
    margin-top: 5px;
    font-size: 13px;
    line-height: 1.2;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: left;
  }
  
  .grey-text {
    color: grey;
    height: 20px;
    font-size: 13px;
    text-align: left;
  }
}


